@import url(https://fonts.googleapis.com/css?family=Open+Sans:300,400,500,400italic,600);

@import url(https://fonts.googleapis.com/css?family=Lato:400,700);

@import url(https://fonts.googleapis.com/css?family=Open+Sans:300,400,500,400italic,600);

@import url(https://fonts.googleapis.com/css?family=Lato:400,700);

/* Mixin */

.chart-td {
  background: rgba(63, 72, 84, 0.95);
}

.preview-chart-wrapper {
  position: relative;
  padding: 0;
}

.preview-chart-wrapper :global .line-chart text {
  fill: #FFFFFF;
}

.preview-chart-wrapper :global .units-label-shadow {
  stroke: rgba(63, 72, 84, 0.95);
  stroke-width: 2.5px;
  opacity: 0.9;
}

.preview-chart-wrapper :global .line-chart .line {
  fill: none;
  stroke: #ccc;
  stroke-width: 1;
}

.chart-title-from-table {
  line-height: 2;
  padding: 0 10px;
}




