@import url(https://fonts.googleapis.com/css?family=Open+Sans:300,400,500,400italic,600);

@import url(https://fonts.googleapis.com/css?family=Lato:400,700);

@import url(https://fonts.googleapis.com/css?family=Open+Sans:300,400,500,400italic,600);

@import url(https://fonts.googleapis.com/css?family=Lato:400,700);

/* Mixin */

.root {
  font-family: "Open Sans", sans-serif;
  color: #000;
  position: relative;
}

.trashGroup {
  composes: btn from '../../Sass/common/_buttons.scss';
  position: absolute;
  color: #08ABD5;
  right: 2px;
  top: 2px;
}

@media (max-width: 768px) {
  .trashGroup {
    top: 10px;
  }
}

.trashGroup svg {
  height: 18px;
  width: 18px;
  margin: 3px;
  fill: #3f4854;
}

.btn--catalog {
  composes: btn from '../../Sass/common/_buttons.scss';
  text-align: left;
  font-size: 0.8125rem;
  padding: 5px 40px;
  position: relative;
  width: 100%;
  font-weight: bold;
  line-height: normal;
  position: relative;
}

.btn--catalog svg {
  display: inline-block;
}

@media (max-width: 768px) {
  .btn--catalog {
    font-size: 0.9rem;
    padding-top: 10px;
    padding-bottom: 10px;
    border-bottom: 1px solid #ddd;
  }
}

.btn--catalog:hover,
.btn--catalog:focus {
  background: #08ABD5;
  color: #fff;
}

.btnCatalogTopLevel {
  font-weight: 600;
  border-bottom: 1px solid #ddd;
  padding-left: 10px;
}

.isPreviewed {
  background: #08ABD5;
  color: #ffffff;
}

.folder {
  position: absolute;
  left: 8px;
  top: 5px;
  opacity: 0.5;
}

.folder svg {
  height: 30px;
  width: 30px;
  display: block;
}

@media (min-width: 768px) {
  .folder {
    top: 0;
  }

  .folder svg {
    height: 25px;
    width: 25px;
  }
}

.caret {
  position: absolute;
  right: 2px;
  top: 7px;
}

.caret svg {
  fill: #3f4854;
  opacity: 0.5;
  height: 25px;
  width: 25px;
}

@media (min-width: 768px) {
  .caret {
    right: 5px;
    top: 5px;
  }

  .caret svg {
    height: 18px;
    width: 18px;
  }
}

.caret.offsetRight {
  right: 20px;
}

.catalog-group {
  composes: list-reset from '../../Sass/common/_base.scss';
  padding-left: 10px;
  padding-top: 5px;
}

.catalog-group--lower-level {
  margin-left: 20px;
  border-left: 1px solid #ddd;
}

.label {
  composes: label from '../../Sass/common/_labels.scss';
  font-size: 0.8125rem;
}

.label--no-results {
  composes: label;
}




