@import url(https://fonts.googleapis.com/css?family=Open+Sans:300,400,500,400italic,600);

@import url(https://fonts.googleapis.com/css?family=Lato:400,700);

@import url(https://fonts.googleapis.com/css?family=Open+Sans:300,400,500,400italic,600);

@import url(https://fonts.googleapis.com/css?family=Lato:400,700);

/* Mixin */

.dropdown {
  position: relative;
}

.list {
  composes: list-reset from "../../Sass/common/_base.scss";
  -moz-transform: scaleY(0);
  -o-transform: scaleY(0);
  -ms-transform: scaleY(0);
  -webkit-transform: scaleY(0);
  transform: scaleY(0);
  -moz-transition: transform 0.1s cubic-bezier(0.19, 1, 0.22, 1);
  -o-transition: transform 0.1s cubic-bezier(0.19, 1, 0.22, 1);
  -webkit-transition: transform 0.1s cubic-bezier(0.19, 1, 0.22, 1);
  transition: transform 0.1s cubic-bezier(0.19, 1, 0.22, 1);
  moz-transform-origin: center top;
  -o-transform-origin: center top;
  -ms-transform-origin: center top;
  -webkit-transform-origin: center top;
  transform-origin: center top;
  position: fixed;
  overflow: hidden;
  z-index: 9999;
  box-sizing: border-box;
  background: #fff;
  border-radius: 2px;
}

.list.is-open {
  transform: scaleY(1);
}

.btn--dropdown {
  composes: btn from "../../Sass/common/_buttons.scss";
  composes: btn-primary from "../../Sass/common/_buttons.scss";
  composes: btn-transparent from "../../Sass/common/_buttons.scss";
  text-align: left;
  background: #FFFFFF;
  color: #595B60;
  border: 1px solid #ddd;
}

.btn--dropdown:hover,
.btn--dropdown:focus {
  background: #FFFFFF;
  color: #595B60;
  border: 1px solid #08ABD5;
}

.btn--dropdown svg {
  width: 30px;
  height: 30px;
  width: 20px;
  height: 20px;
  position: absolute;
  right: 10px;
  top: 12px;
  fill: #595B60;
}

.btn--option {
  composes: btn from "../../Sass/common/_buttons.scss";
  composes: btn-transparent from "../../Sass/common/_buttons.scss";
  composes: btn--small from "../../Sass/common/_buttons.scss";
  width: 100%;
  color: #000;
}

.btn--option:hover,
.btn--option:focus {
  color: orange;
}

.btn--option.is-selected {
  color: #08ABD5;
}

.myDataDropdown {
  text-align: left;
}




