@import url(https://fonts.googleapis.com/css?family=Open+Sans:300,400,500,400italic,600);

@import url(https://fonts.googleapis.com/css?family=Lato:400,700);

@import url(https://fonts.googleapis.com/css?family=Open+Sans:300,400,500,400italic,600);

@import url(https://fonts.googleapis.com/css?family=Lato:400,700);

/* Mixin */

.chart :global .line-chart .axis path,
.chart :global .line-chart .axis line {
  fill: none;
  stroke: none;
  stroke-width: 1px;
  shape-rendering: crispEdges;
}

.chart :global .line-chart .selection circle {
  fill: white;
  stroke: white;
  stroke-width: 1;
  opacity: 0.65;
}

.chart :global .line-chart .selection line {
  stroke: white;
  opacity: 0.65;
  stroke-width: 1;
}

.chart :global .line-chart .plot-area {
  fill: none;
}

.chart :global .line-chart path.line {
  stroke-width: 2;
}

.chart :global .linechart-title {
  left: 0;
  right: 0;
  margin: 0 auto;
  position: absolute;
  text-align: center;
  overflow: hidden;
  height: 18px;
}

.chart :global .linechart-title .color {
  display: inline-block;
  width: 16px;
  height: 2px;
  margin: 0 5px 4px 7px;
}

.chart :global .linechart-title .name {
  margin-right: 7px;
  font-family: monospace;
  font-size: 110%;
}

.tool-tip {
  color: #FFFFFF;
  z-index: 10;
  background-color: #2e343d;
  padding: 5px 5px 10px 10px;
  font-family: monospace;
}

.tool-tip :global table {
  border-collapse: collapse;
  border-spacing: 0;
  max-width: 100%;
  width: 100%;
}

.tool-tip :global .color {
  width: 16px;
  height: 2px;
  margin: 0 0 4px 0;
  display: inline-block;
  margin-right: 11px;
  margin-left: -5px;
}

.tool-tip :global .value {
  min-width: 80px;
}

.tool-tip :global p {
  margin: 1px 0;
}

.tool-tip :global .x-value {
  margin-bottom: 10px;
}

.tool-tip :global .category-name {
  margin-top: 5px;
}

.tool-tip :global td {
  padding-top: 2px;
  padding-bottom: 2px;
}




