@import url(https://fonts.googleapis.com/css?family=Open+Sans:300,400,500,400italic,600);

@import url(https://fonts.googleapis.com/css?family=Lato:400,700);

@import url(https://fonts.googleapis.com/css?family=Open+Sans:300,400,500,400italic,600);

@import url(https://fonts.googleapis.com/css?family=Lato:400,700);

/* Mixin */

.data-catalog {
  composes: list-reset from '../../Sass/common/_base.scss';
  composes: scrollbars from '../../Sass/common/_base.scss';
  font-size: 0.9rem;
  overflow-y: auto;
  overflow-x: hidden;
  box-sizing: border-box;
  position: absolute;
  left: 0;
  right: 0;
  bottom: 0;
  top: 40px;
}

@media (min-width: 768px) {
  .data-catalog {
    padding-right: 5px;
    height: calc(100% - 70px);
    position: static;
  }
}

.label {
  composes: label from '../../Sass/common/_labels.scss';
  font-size: 0.8125rem;
}




