@import url(https://fonts.googleapis.com/css?family=Open+Sans:300,400,500,400italic,600);

@import url(https://fonts.googleapis.com/css?family=Lato:400,700);

.input {
  font-family: inherit;
  font-size: 0.9rem;
  box-sizing: border-box;
  margin-top: 0;
  margin-bottom: 0;
  border-style: solid;
  border-width: 1px;
  border-color: #ddd;
  border-radius: 2px;
  width: 100%;
}

.input[type=text],
.input[type=date],
.input[type=datetime],
.input[type=datetime-local],
.input[type=email],
.input[type=month],
.input[type=number],
.input[type=password],
.input[type=search],
.input[type=tel],
.input[type=time],
.input[type=url],
.input[type=week] {
  height: 40px;
  color: #888;
  display: block;
  padding: .5rem .5rem;
  vertical-align: middle;
  -webkit-appearance: none;
}

.field {
  composes: input;
  border-style: solid;
  border-width: 1px;
  border-color: #ddd;
  border-radius: 2px;
  width: 100%;
}

.field:focus,
.field.is-focused {
  outline: none;
  border-color: rgba(8, 171, 213, 0.5);
  box-shadow: none;
}

.field:disabled,
.field.is-disabled {
  background-color: rgba(0, 0, 0, 0.125);
  opacity: .5;
}

.field:read-only:not(select),
.field.is-read-only {
  background-color: rgba(0, 0, 0, 0.125);
}




