/* Mixin */

.content table {
  clear: both;
}

.content table:before,
.content table:after {
  content: " ";
  display: table;
  clear: both;
}

.time-series-chart {
  /*!*/
}

.section {
  composes: clearfix from '../../Sass/common/_base.scss';
  background: rgba(255, 255, 255, 0.15);
  border-top: 1px solid #595B60;
}

.title {
  composes: btn from './feature-info-panel.scss';
  position: relative;
  width: 100%;
  background: #3F4854;
  min-height: 40px;
  margin: 0;
  padding-right: 30px;
}

.title span {
  display: block;
  font-weight: 300;
  font-size: 0.9rem;
}

.title svg {
  height: 20px;
  width: 20px;
  fill: #fff;
  float: right;
  position: absolute;
  top: 10px;
  right: 5px;
}

.content {
  padding: 5px;
  position: relative;
}

.content table {
  font-family: monospace, sans-serif;
  max-width: 100%;
  border-collapse: collapse;
}

.content td {
  position: relative;
  border-collapse: collapse;
  border: 1px solid #3F4854;
  padding: 5px;
}

.raw-data-button {
  composes: btn from '../../Sass/common/_buttons.scss';
  composes: btn-primary from '../../Sass/common/_buttons.scss';
  composes: btn--small from '../../Sass/common/_buttons.scss';
  width: auto;
  float: right;
  margin-bottom: 13px;
  margin-right: 1px;
  margin-top: 10px;
}




