@import url(https://fonts.googleapis.com/css?family=Open+Sans:300,400,500,400italic,600);

@import url(https://fonts.googleapis.com/css?family=Lato:400,700);

@import url(https://fonts.googleapis.com/css?family=Open+Sans:300,400,500,400italic,600);

@import url(https://fonts.googleapis.com/css?family=Lato:400,700);

/* Mixin */

.dropdown {
  float: left;
}

.dropdown__list {
  display: flex;
}

.dropdown__btn {
  composes: btn--small from '../../../Sass/common/_buttons.scss';
}

.dropdown__btn--option {
  width: auto;
  float: left;
  color: #08ABD5;
}

.dropdown__btn--option:hover,
.dropdown__btn--option:focus {
  background: #08ABD5;
  color: orange;
  text-decoration: none;
}

.chart-expand {
  z-index: 999;
  position: absolute;
  right: 0;
}

.raise-to-title {
  top: -21px;
}

.chart-dropdown-button {
  position: relative;
  float: right;
}

.btn--dropdown {
  z-index: 999;
  color: white;
  font-family: "Open Sans", sans-serif;
  border: none;
  padding: 1px 8px;
  margin-right: 3px;
  width: auto;
}

.btn-small {
  composes: btn--small from '../../../Sass/common/_buttons.scss';
}

.btn-download {
  composes: btn from '../../../Sass/common/_buttons.scss';
  text-align: center;
  padding: 4px 3px;
  margin-left: 3px;
  vertical-align: top;
}

.btn-download svg {
  height: 20px;
  width: 20px;
}

.btn-download:hover,
.btn-download:focus {
  text-decoration: none;
}

.btn--chart-expand {
  composes: btn from '../../../Sass/common/_buttons.scss';
  composes: btn-primary from '../../../Sass/common/_buttons.scss';
  display: inline-block;
  z-index: 999;
  padding: 1px 8px;
  right: 0;
  margin-right: 3px;
  width: 65px;
}

.a--download {
  composes: btn--download from '../../../Sass/common/_buttons.scss';
  composes: btn-primary from '../../../Sass/common/_buttons.scss';
  display: inline-block;
  text-align: center;
  padding: 1px 5px 0 5px;
  margin-left: 3px;
  width: 30px;
  vertical-align: -5px;
}

.a--download svg {
  height: 20px;
  width: 20px;
}

.a--download:hover,
.a--download:focus {
  text-decoration: none;
}




