@import url(https://fonts.googleapis.com/css?family=Open+Sans:300,400,500,400italic,600);

@import url(https://fonts.googleapis.com/css?family=Lato:400,700);

/* Mixin */

@import url(https://fonts.googleapis.com/css?family=Open+Sans:300,400,500,400italic,600);

@import url(https://fonts.googleapis.com/css?family=Lato:400,700);

.timeline {
  display: table;
  width: 100%;
  padding-top: 5px;
  padding-bottom: 5px;
  padding-right: 5px;
  font-family: "Open Sans", sans-serif;
}

.text-row {
  display: block;
  color: #fff;
  font-size: 0.9rem;
  position: absolute;
  bottom: 44px;
  left: 0px;
  background: #3F4854;
  padding: 3px 5px;
}

.text-cell {
  display: table-cell;
}

.time {
  padding-left: 15px;
}

.controlsRow {
  display: table-row;
}

.style-selector {
  font-size: 0.9rem;
}

.style-selector label {
  margin-right: 5px;
}

.title {
  display: block;
  margin-top: 10px;
  margin-bottom: 5px;
  font-weight: bold;
}

.field {
  composes: field from '../../../Sass/common/_form.scss';
  color: black;
}

.timelineDatePicker {
  color: #fff;
  display: table-cell;
  width: 30px;
  height: 30px;
}

.datePicker {
  width: 260px;
  height: 300px;
  border: 1px solid #888888;
  border-radius: 5px;
  padding: 5px;
  position: absolute;
  left: 10px;
  bottom: 70px;
  background: #3F4854;
  z-index: 100;
}

.datePicker.openBelow {
  top: 40px;
  left: -190px;
}

.dayPicker {
  text-align: center;
  margin-top: -10px;
}

.backbutton,
.backbtn {
  composes: btn from '../../../Sass/common/_buttons.scss';
  composes: btn-transparent from '../../../Sass/common/_buttons.scss';
  display: inline-block;
  z-index: 99;
  position: relative;
}

.backbutton svg,
.backbtn svg {
  height: 20px;
  width: 25px;
  fill: #ffffff;
  display: inline-block;
  vertical-align: bottom;
}

.backbutton[disabled],
.backbutton:disabled,
.backbtn[disabled],
.backbtn:disabled {
  opacity: 0.1;
}

.backbtn {
  padding-bottom: 5px;
  padding-right: 5px;
}

.togglebutton {
  background: transparent;
  padding: 0px 6px;
  border: 0;
  height: 30px;
  width: 30px;
  margin-right: 5px;
  display: inline-block;
}

.togglebutton svg {
  width: 18px;
  height: 18px;
  float: left;
  fill: #fff;
}

.grid {
  display: block;
  width: 100%;
  height: 100%;
  margin: 0 auto;
  color: #fff;
  padding: 0px 5px;
  border-radius: 3px;
  margin-top: -20px;
}

.gridBody {
  height: calc(100% - 30px);
  overflow: auto;
  composes: scrollbars from '../../../Sass/common/_base.scss';
}

.gridRow:hover {
  background: rgba(255, 255, 255, 0.15);
  cursor: pointer;
}

.inactiveGridRow:hover {
  background: transparent;
  cursor: default;
}

.gridLabel {
  float: left;
  display: inline-block;
  width: 35px;
  font-size: 10px;
  padding-left: 3px;
}

.gridRowInner12,
.gridRowInner31 {
  display: table-row;
  padding: 3px;
  border-radius: 3px;
}

.gridRowInner12 span,
.gridRowInner31 span {
  display: inline-block;
  height: 10px;
  width: 2px;
  background: rgba(255, 255, 255, 0.15);
  margin-top: 1px;
}

.gridRowInner12 span {
  margin-right: 11px;
}

.gridRowInner31 span {
  margin-right: 3px;
}

.activeGrid {
  opacity: 0.9;
  background: #08ABD5 !important;
}

.gridHeading {
  text-align: center;
  color: #fff;
  font-size: 12px;
}

.summary {
  font-size: 10px;
}

.centuryBtn,
.dateBtn {
  composes: btn from '../../../Sass/common/_buttons.scss';
  composes: btn-primary from '../../../Sass/common/_buttons.scss';
  width: calc(100% - 20px);
  margin: 5px;
}

.centuryBtn {
  display: inline-block;
  width: 40%;
}




