/* Mixin */

.download {
  float: right;
  margin: 10px 1px;
}

.btn {
  composes: btn from './feature-info-panel.scss';
  composes: btn-primary from '../../Sass/common/_buttons.scss';
}

.icon--download {
  display: inline-block;
  vertical-align: bottom;
}

.icon--download svg {
  height: 20px;
  width: 20px;
  fill: #FFFFFF;
}

.dropdown__list {
  display: flex;
}

.dropdown__button {
  composes: btn--small from '../../Sass/common/_buttons.scss';
}




