@import url(https://fonts.googleapis.com/css?family=Open+Sans:300,400,500,400italic,600);

@import url(https://fonts.googleapis.com/css?family=Lato:400,700);

@import url(https://fonts.googleapis.com/css?family=Open+Sans:300,400,500,400italic,600);

@import url(https://fonts.googleapis.com/css?family=Lato:400,700);

/* Mixin */

.btn-catalog-item {
  /* These are technically the same, but use both */
  overflow-wrap: break-word;
  word-wrap: break-word;
  word-break: break-word;
}

.root {
  composes: clearfix from '../../Sass/common/_base.scss';
  display: block;
  width: 100%;
  position: relative;
}

.btn-catalog-item {
  font-size: 0.8125rem;
  composes: btn from '../../Sass/common/_buttons.scss';
  position: relative;
  width: 100%;
  padding: 5px 25px 5px 15px;
}

.btn-catalog-item:focus,
.btn-catalog-item:hover {
  color: #08ABD5;
}

.btn-catalog-item:focus:before,
.btn-catalog-item:hover:before {
  opacity: 1;
}

.btn-catalog-item--is-previewed {
  color: #08ABD5;
  font-weight: 600;
}

.btn-catalog-item--is-previewed:before {
  opacity: 1;
}

@media (max-width: 768px) {
  .btn-catalog-item {
    font-size: 0.9rem;
    padding-top: 10px;
    padding-bottom: 10px;
    border-bottom: 1px solid #ddd;
  }
}

.btn-action {
  composes: btn from '../../Sass/common/_buttons.scss';
  position: absolute;
  color: #08ABD5;
  right: 0;
  top: 0;
}

@media (max-width: 768px) {
  .btn-action {
    top: 5px;
  }
}

.btn-action svg {
  height: 18px;
  width: 18px;
  margin: 5px;
  fill: #3f4854;
}

.btn-action:hover svg,
.btn-action:focus svg {
  fill: #08ABD5;
}

.btn-action--loading-on-map {
  top: 3px;
  right: 12px;
}

.btn-action--loading-on-map:before {
  position: absolute;
}




